.contacto{
    padding-top: 5rem;
    .contenido{
        width: 100%;
        background-color: $color_azul_contacto;
        filter: brightness(80%);
        box-shadow: $color_sombra_card 0px 0px 1rem;
        border-radius: 3rem;
        // filter: blur(5px);
        form{
            font-family: $fuente_heading;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-flow: column;
            margin: 0 auto 7rem auto;
            width: 80%;
            padding: 2rem;
            @include telefono{
                width: 100%;
            }
            input, textarea{
                border: none;
                resize: none;
                outline: none;
                
                color: $color_blanco;
                background-color: $color_azul_contacto;
                
                padding: 1.5rem 0;
                margin: 2rem;
                width: 80%;
                
                border-bottom: 1px solid rgb(160, 158, 158);
                transition: border-bottom .3s ease;
            }
            :focus{
                border-bottom: 2px solid rgb(255, 255, 255);
            }
            input::placeholder, textarea::placeholder{
                color: $color_blanco;
            }
            input[type=submit]{
                font-family: $fuente_p;
                max-width: 20rem;
                border: none;
                padding: 2rem;
                border-radius: 3rem;
                margin-top: 3rem;
                transition: box-shadow .3s ease;
                background-color: $color_azul_contacto;
                @include telefono{
                    box-shadow: $color_sombra_card 0px 0px 1rem;
                }
            }
            input[type=submit]:hover{
                box-shadow: $color_sombra_card 0px 0px 1rem;
            }
        }
    }
}