.logo{
    // color: $color_logo;
    color: purple;
    margin-left: 4rem;
    .logo_nombre{
        font-family: $fuente_logo;
        font-weight: 300;
        font-size: 3rem;
    }
    .logo_bold{
        font-family: $fuente_logo;
        font-weight: 700;
        font-size: 3rem;
    }
    @include telefono{
        margin-left: inherit;
    }
    @include ipad{
        margin-left: inherit;
    }
}

.nav{
    a{
        color: $color_azul_oscuro;
        margin-right: 2rem;
        font-family: $fuente_nav;
        :last-of-type{
            margin-right: 0;
        }
        @include telefono{
            margin-bottom: 1.2rem;
            margin-right: inherit;
        }
    }
    span{
        font-family: $fuente_nav;
        color: $color_azul_claro;
        letter-spacing: -.2rem;
        font-size: 2rem;
        transition: color .2s ease;
    }
    span:hover{
        color: $color_azul_oscuro;
    }
    @include telefono{
        display: flex;
        flex-flow: column;
    }
}

.curriculum{
    border: #A3EEFA solid 1px;
    border-radius: 2rem;
    margin-right: 4rem;
    padding: 1.2rem;
    color: #CFCED3;
    @include telefono{
        margin-right: inherit;
    }
    @include ipad{
        margin-top: 3rem;
        margin-right: inherit;
    }
}

.info{
    width: 60%;
    margin: 12rem auto 40rem;
    cursor: default;
    h1{
        font-size: 7rem;
        margin: 0;
        font-weight: bold;
        color: #CFCED3;
        @include telefono{
            font-size: 6rem;
            line-height: 1;
        }
    }
    h2{
        font-size: 5.6rem;
        font-weight: normal;
        color: #83769e;
        margin-top: -1rem;
        margin-bottom: 2.5rem;
        @include telefono{
            margin-top: 1.4rem;
            font-size: 4.5rem;
            line-height: 1;
        }
        @include ipad{
            margin-top: .6rem;
            font-size: 5rem;
        }
    }
    p{
        font-family: $fuente_p;
        font-weight: 300;
        line-height: 1.5;
        color: rgba(255, 255, 255, 0.774);
    }
    @include telefono{
        width: 90%;
    }
    @include ipad{
        width: 90%;
    }
}