.proyectos{
    padding-top: 5rem;
    margin-bottom: 5rem;
    .grid-2{
        display: grid;
        grid-template-columns: 70% 25%;
        column-gap: 5%;
        cursor: default;

        margin-top: 6rem;
        margin-bottom: 15rem;
        img{
            border-radius: 3rem;
            filter: brightness(50%);
            @include telefono{
                order: 1;
            }
        }
        .texto{
            text-align: right;
            margin: 0 auto;
            .p-1{
                color: $color_azul_claro;
                font-size: 2rem;
            }
            .p-2{
                color: $color_azul_oscuro;
                margin-top: -1rem;
            }
            .p-3{
                color: $color_blanco;
                line-height: 1.5;
                font-size: 1.4rem;
                font-family: $fuente_p;
                padding: 1rem 2rem;
                border-radius: 2rem;
                background-color: $color_azul_proyectos;
                margin-top: 3rem;
                margin-left: -30rem;
            
                position: relative;
                @include telefono{
                    margin: 1rem .2rem 3rem .2rem;
                }
            }
            @include telefono{
                margin-top: -6rem;
            }
        }
        @include telefono{
            grid-template-columns: none;
            grid-template-rows: repeat(2, 1fr);
        }
    }
    .grid-3{
        display: grid;
        grid-template-columns: 25% 70%;
        column-gap: 5%;
        cursor: default;
        img{
            border-radius: 3rem;
            filter: brightness(50%);
            position: relative;
            order: 1;
        }
        .texto-in{
            margin: 0 auto;
            .p-1{
                color: $color_azul_claro;
                font-size: 2rem;
            }
            .p-2{
                color: $color_azul_oscuro;
                margin-top: -1rem;
            }
            .p-3{
                color: $color_blanco;
                line-height: 1.5;
                font-size: 1.4rem;
                font-family: $fuente_p;
                padding: 1rem 2rem;
                border-radius: 2rem;
                background-color: $color_azul_proyectos;
                margin-top: 3rem;
                margin-right: -30rem;
                
                position: relative;
                z-index: 2;
                @include telefono{
                    margin: 1rem .2rem 3rem .2rem;
                }
            }
            @include telefono{
                margin-top: -20rem;
            }
        }
        @include telefono{
            grid-template-columns: none;
            grid-template-rows: repeat(2, 1fr);
        }
    }
}




