html{
    box-sizing: border-box;
    font-size: 62.5%;
    scrollbar-width: none;
}

::-webkit-scrollbar{
    display: none;
}

*, *::before, *::after{
    box-sizing: inherit;
    font-size: 1.6rem;
    font-family: $fuente_default;    
}

body{
    background-color: $color_fondo;
    overflow: hidden;
}

a{
    text-decoration: none;
}

img{
    max-width: 100%;
}

h1, h2, h3, h4{
    font-family: $fuente_heading;
}
h4{
    display: inline-block;
    cursor: default;
    color: $color_azul_claro;
    font-size: 2.4rem;
    transition: color .3s ease;
    span{
        font-weight: bold;
        color: $color_azul_oscuro;
    }
}
h4:hover{
    color: $color_azul_oscuro;
}

//Clases Reutilizables
.display-flex{
    margin-top: -1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    // position: fixed;
    // top: 0;
    // left: 0;
    // width: 100%;
    @include telefono{
        flex-flow: column;
    }
    @include ipad{
        flex-flow: column;
    }
}

.contenedor{
    max-width: 110rem;
    width: 90%;
    margin: 0 auto;
}

